@import "../../styles/variables";

.requestButton {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 85%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 6px;
  color: $color-secondary-text;
  font-size: 18px;
  padding: $space-2;
  letter-spacing: 3px;
  line-height: 1.3;
}
